import { useQuery, useMutation, useQueryClient } from '@tanstack/react-query';
import projectsService from '../services/projects';
import tasksService from '../services/tasks';
import activityLogsService from '../services/activityLogs';
import projectMessagesService from '../services/projectMessages';

export const useProjects = (userId) => {
  return useQuery({
    queryKey: ['projects', userId],
    queryFn: async () => {
      return await projectsService.listProjects(userId);
    },
    enabled: !!userId,
    staleTime: 2 * 60 * 1000, // 2 minutes
  });
};

export const useProject = (projectId, userId) => {
  return useQuery({
    queryKey: ['project', projectId],
    queryFn: async () => {
      return await projectsService.getProject(projectId);
    },
    enabled: !!projectId && !!userId,
    staleTime: 2 * 60 * 1000,
  });
};

export const useProjectTasks = (projectId) => {
  return useQuery({
    queryKey: ['tasks', projectId],
    queryFn: async () => {
      return await tasksService.listTasks(projectId);
    },
    enabled: !!projectId,
    staleTime: 1 * 60 * 1000, // 1 minute
  });
};

export const useProjectMessages = (projectId, taskId = null) => {
  return useQuery({
    queryKey: ['messages', projectId, taskId],
    queryFn: async () => {
      return await projectMessagesService.listMessages({ projectId, taskId, sortBy: 'newest' });
    },
    enabled: !!projectId,
    staleTime: 30 * 1000, // 30 seconds
  });
};

export const useActivityLogs = (projectId, taskId = null) => {
  return useQuery({
    queryKey: ['activityLogs', projectId, taskId],
    queryFn: async () => {
      return await activityLogsService.listActivityLogs({ projectId, taskId, sortBy: 'newest' });
    },
    enabled: !!projectId,
    staleTime: 1 * 60 * 1000, // 1 minute
  });
};

export const useCreateProject = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async ({ userId, projectData }) => {
      return await projectsService.createProject(userId, projectData);
    },
    onSuccess: (data, variables) => {
      queryClient.invalidateQueries(['projects', variables.userId]);
    },
  });
};

export const useCreateTask = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async ({ projectId, taskData, userId }) => {
      return await tasksService.createTask(projectId, taskData, userId);
    },
    onSuccess: (data, variables) => {
      queryClient.invalidateQueries(['tasks', variables.projectId]);
      queryClient.invalidateQueries(['activityLogs', variables.projectId]);
    },
  });
};

export const useCreateMessage = () => {
  const queryClient = useQueryClient();
  
  return useMutation({
    mutationFn: async (messageData) => {
      return await projectMessagesService.createMessage(messageData);
    },
    onSuccess: (data, variables) => {
      queryClient.invalidateQueries(['messages', variables.projectId, variables.taskId]);
      queryClient.invalidateQueries(['activityLogs', variables.projectId]);
    },
  });
};

