import React, { useState, useEffect } from 'react';
import {
  Box,
  Container,
  Typography,
  Button,
  Chip,
  Grid,
  Card,
  CardContent,
  CardActions,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Alert,
  Tabs,
  Tab,
  List,
  ListItem,
  ListItemText,
  ListItemSecondaryAction,
  Divider
} from '@mui/material';
import {
  Edit as EditIcon,
  Delete as DeleteIcon,
  Add as AddIcon,
  CalendarToday as CalendarIcon,
  PersonAdd as PersonAddIcon,
  CheckCircle as CheckCircleIcon,
  RadioButtonUnchecked as RadioButtonUncheckedIcon,
  Message as MessageIcon
} from '@mui/icons-material';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import { 
  useProject, 
  useProjectTasks, 
  useProjectMessages, 
  useActivityLogs, 
  useCreateTask, 
  useCreateMessage 
} from '../../hooks/useProjects';
import dayjs from 'dayjs';

const ProjectView = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const { user } = useAuth();
  const [filteredLogs, setFilteredLogs] = useState([]);
  const [logFilter, setLogFilter] = useState('all');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [activeTab, setActiveTab] = useState(0);
  const [taskDialogOpen, setTaskDialogOpen] = useState(false);
  const [addMemberDialogOpen, setAddMemberDialogOpen] = useState(false);
  const [messageDialogOpen, setMessageDialogOpen] = useState(false);
  const [newMemberEmail, setNewMemberEmail] = useState('');
  const [taskFormData, setTaskFormData] = useState({
    title: '',
    description: '',
    dueDate: '',
    priority: 'Medium',
    status: 'Not Started'
  });
  const [messageFormData, setMessageFormData] = useState({
    message: '',
    messageDate: dayjs().format('YYYY-MM-DD')
  });

  // React Query hooks - parallel fetching
  const { data: project, isLoading: projectLoading } = useProject(id, user?.$id);
  const { data: tasks = [], isLoading: tasksLoading } = useProjectTasks(id);
  const { data: messages = [], isLoading: messagesLoading } = useProjectMessages(id);
  const { data: activityLogs = [], isLoading: logsLoading } = useActivityLogs(id);
  const createTaskMutation = useCreateTask();
  const createMessageMutation = useCreateMessage();

  // Filter logs based on logFilter
  useEffect(() => {
    if (logFilter === 'all') {
      setFilteredLogs(activityLogs);
    } else {
      setFilteredLogs(activityLogs.filter(log => log.category === logFilter));
    }
  }, [logFilter, activityLogs]);

  const handleCreateTask = async () => {
    if (!taskFormData.title.trim()) {
      setError('Task title is required');
      return;
    }

    setLoading(true);
    try {
      await createTaskMutation.mutateAsync({
        projectId: id,
        taskData: taskFormData,
        userId: user.$id
      });
      
      // Log activity
      const activityLogsService = (await import('../../services/activityLogs')).default;
      await activityLogsService.createActivityLog({
        projectId: id,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Task Update',
        action: `Created task: ${taskFormData.title}`,
        newValue: taskFormData.title
      });

      setSuccess('Task created successfully');
      setTaskDialogOpen(false);
      setTaskFormData({
        title: '',
        description: '',
        dueDate: '',
        priority: 'Medium',
        status: 'Not Started'
      });
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      if (err.code === 404) {
        setError('Task feature is not set up yet. Please contact an administrator.');
      } else if (err.message?.includes('permissions')) {
        setError('You do not have permission to create tasks in this project.');
      } else {
        setError(err?.message || 'Failed to create task');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleCreateMessage = async () => {
    if (!messageFormData.message.trim()) {
      setError('Message is required');
      return;
    }

    setLoading(true);
    try {
      await createMessageMutation.mutateAsync({
        projectId: id,
        userId: user.$id,
        userName: user.name || user.email,
        message: messageFormData.message,
        messageDate: messageFormData.messageDate
      });

      // Log activity
      const activityLogsService = (await import('../../services/activityLogs')).default;
      await activityLogsService.createActivityLog({
        projectId: id,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Comment Added',
        action: `Posted a message`,
        newValue: messageFormData.message.substring(0, 50) + '...'
      });

      setSuccess('Message posted successfully');
      setMessageDialogOpen(false);
      setMessageFormData({
        message: '',
        messageDate: dayjs().format('YYYY-MM-DD')
      });
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      if (err.code === 404) {
        setError('Messages feature is not set up yet. Please contact an administrator.');
      } else if (err.message?.includes('permissions')) {
        setError('You do not have permission to post messages in this project.');
      } else {
        setError(err?.message || 'Failed to post message');
      }
    } finally {
      setLoading(false);
    }
  };

  const handleAddMember = async () => {
    if (!newMemberEmail.trim()) {
      setError('Please enter an email address');
      return;
    }

    try {
      const projectsService = (await import('../../services/projects')).default;
      await projectsService.addUserToAccessGroup(id, newMemberEmail.trim());
      
      // Log activity
      const activityLogsService = (await import('../../services/activityLogs')).default;
      await activityLogsService.createActivityLog({
        projectId: id,
        userId: user.$id,
        userName: user.name || user.email,
        category: 'Project Update',
        action: `Added ${newMemberEmail} to access group`,
        newValue: newMemberEmail
      });

      setSuccess('Member added successfully');
      setAddMemberDialogOpen(false);
      setNewMemberEmail('');
      setTimeout(() => setSuccess(''), 3000);
    } catch (err) {
      if (err.code === 404) {
        setError('User not found. Please check the email address.');
      } else if (err.message?.includes('already')) {
        setError('User is already a member of this project.');
      } else {
        setError(err?.message || 'Failed to add member');
      }
    }
  };

  const getPriorityColor = (priority) => {
    const colors = {
      'Low': 'success',
      'Medium': 'warning',
      'High': 'error',
      'Critical': 'error'
    };
    return colors[priority] || 'default';
  };

  const getStatusColor = (status) => {
    const colors = {
      'Not Started': 'default',
      'Active': 'primary',
      'Blocked': 'warning',
      'Completed': 'success'
    };
    return colors[status] || 'default';
  };

  const isOverdue = (dueDate) => {
    if (!dueDate) return false;
    return new Date(dueDate) < new Date() && new Date(dueDate).toDateString() !== new Date().toDateString();
  };

  if (projectLoading || !project) {
    return (
      <Container maxWidth="lg" sx={{ py: 4 }}>
        <Typography>Loading project...</Typography>
      </Container>
    );
  }

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Box sx={{ mb: 4, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
        <Box>
          <Typography variant="h4" sx={{ fontWeight: 'bold', color: 'primary.main', mb: 1 }}>
            {project.name}
          </Typography>
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            <Chip
              label={project.status}
              size="small"
              color={project.status === 'Active' ? 'primary' : project.status === 'Completed' ? 'success' : 'default'}
            />
            <Typography variant="body2" color="text.secondary">
              ID: {project.projectId}
            </Typography>
            <Box
              sx={{
                width: 20,
                height: 20,
                borderRadius: '50%',
                backgroundColor: project.colorTag || '#1976d2',
                border: '1px solid #ccc'
              }}
            />
          </Box>
        </Box>
        <Box sx={{ display: 'flex', gap: 1 }}>
          <Button
            variant="outlined"
            startIcon={<PersonAddIcon />}
            onClick={() => setAddMemberDialogOpen(true)}
            sx={{ borderRadius: 1, textTransform: 'none' }}
          >
            Add Member
          </Button>
          <Button
            variant="outlined"
            startIcon={<EditIcon />}
            onClick={() => navigate(`/projects/${id}/edit`)}
            sx={{ borderRadius: 1, textTransform: 'none' }}
          >
            Edit
          </Button>
        </Box>
      </Box>

      {error && (
        <Alert severity="error" sx={{ mb: 2 }} onClose={() => setError('')}>
          {error}
        </Alert>
      )}
      {success && (
        <Alert severity="success" sx={{ mb: 2 }} onClose={() => setSuccess('')}>
          {success}
        </Alert>
      )}

      <Typography variant="body1" sx={{ mb: 3, color: 'text.secondary' }}>
        {project.description || 'No description'}
      </Typography>

      <Box sx={{ borderBottom: 1, borderColor: 'divider', mb: 3 }}>
        <Tabs value={activeTab} onChange={(e, newValue) => setActiveTab(newValue)}>
          <Tab label="Tasks" />
          <Tab label="Messages" />
          <Tab label="Activity Log" />
        </Tabs>
      </Box>

      {activeTab === 0 && (
        <Box>
          <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">Tasks</Typography>
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={() => setTaskDialogOpen(true)}
              sx={{ borderRadius: 1, textTransform: 'none' }}
            >
              Add Task
            </Button>
          </Box>

          <Grid container spacing={2}>
            {tasks.length === 0 ? (
              <Grid item xs={12}>
                <Card>
                  <CardContent sx={{ textAlign: 'center', py: 4 }}>
                    <Typography color="text.secondary">No tasks yet. Create your first task!</Typography>
                  </CardContent>
                </Card>
              </Grid>
            ) : (
              tasks.map((task) => (
                <Grid item xs={12} sm={6} md={4} key={task.$id}>
                  <Card
                    sx={{
                      height: '100%',
                      borderLeft: `4px solid ${
                        task.priority === 'Critical' ? '#d32f2f' :
                        task.priority === 'High' ? '#f57c00' :
                        task.priority === 'Medium' ? '#fbc02d' : '#388e3c'
                      }`,
                      ...(isOverdue(task.dueDate) && {
                        border: '2px solid #d32f2f',
                        backgroundColor: 'error.lighter'
                      })
                    }}
                  >
                    <CardContent>
                      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-start', mb: 1 }}>
                        <Typography variant="h6" sx={{ fontWeight: 'medium', flexGrow: 1 }}>
                          {task.title}
                        </Typography>
                        <Chip
                          label={task.priority}
                          size="small"
                          color={getPriorityColor(task.priority)}
                        />
                      </Box>
                      <Typography variant="body2" color="text.secondary" sx={{ mb: 1, minHeight: 40 }}>
                        {task.description || 'No description'}
                      </Typography>
                      {task.dueDate && (
                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5, mb: 1 }}>
                          <CalendarIcon fontSize="small" color={isOverdue(task.dueDate) ? 'error' : 'action'} />
                      <Typography
                        variant="caption"
                        color={isOverdue(task.dueDate) ? 'error.main' : 'text.secondary'}
                        sx={{ fontWeight: isOverdue(task.dueDate) ? 'bold' : 'normal' }}
                      >
                        {dayjs(task.dueDate).format('MMM D, YYYY')}
                        {isOverdue(task.dueDate) && ' (Overdue)'}
                      </Typography>
                      <Button
                        size="small"
                        onClick={() => navigate(`/calendar?date=${dayjs(task.dueDate).format('YYYY-MM-DD')}`)}
                        sx={{ ml: 1, textTransform: 'none', fontSize: '0.7rem' }}
                      >
                        View in Calendar
                      </Button>
                        </Box>
                      )}
                      <Chip
                        label={task.status}
                        size="small"
                        color={getStatusColor(task.status)}
                        sx={{ mt: 1 }}
                      />
                    </CardContent>
                    <CardActions>
                      <Button
                        size="small"
                        onClick={() => navigate(`/projects/${id}/tasks/${task.$id}`)}
                        sx={{ textTransform: 'none' }}
                      >
                        View Details
                      </Button>
                    </CardActions>
                  </Card>
                </Grid>
              ))
            )}
          </Grid>
        </Box>
      )}

      {activeTab === 1 && (
        <Box>
          <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">Project Messages</Typography>
            <Button
              variant="contained"
              startIcon={<AddIcon />}
              onClick={() => setMessageDialogOpen(true)}
              sx={{ borderRadius: 1, textTransform: 'none' }}
            >
              Post Message
            </Button>
          </Box>

          <List>
            {messages.length === 0 ? (
              <ListItem>
                <ListItemText primary="No messages yet. Post an update to keep the team informed!" />
              </ListItem>
            ) : (
              messages.map((msg, index) => (
                <React.Fragment key={msg.$id}>
                  <ListItem>
                    <ListItemText
                      primary={msg.message}
                      secondary={
                        <Box>
                          <Typography variant="caption" color="text.secondary">
                            {msg.userName}  {dayjs(msg.messageDate).format('MMMM D, YYYY')}  {dayjs(msg.createdAt).format('h:mm A')}
                          </Typography>
                        </Box>
                      }
                    />
                  </ListItem>
                  {index < messages.length - 1 && <Divider />}
                </React.Fragment>
              ))
            )}
          </List>
        </Box>
      )}

      {activeTab === 2 && (
        <Box>
          <Box sx={{ mb: 2, display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
            <Typography variant="h6">Activity Log</Typography>
            <FormControl size="small" sx={{ minWidth: 150 }}>
              <InputLabel>Filter</InputLabel>
              <Select
                value={logFilter}
                onChange={(e) => setLogFilter(e.target.value)}
                label="Filter"
              >
                <MenuItem value="all">All Activity</MenuItem>
                <MenuItem value="Project Update">Project Updates</MenuItem>
                <MenuItem value="Task Update">Task Updates</MenuItem>
                <MenuItem value="Status Change">Status Changes</MenuItem>
                <MenuItem value="Comment Added">Comments</MenuItem>
              </Select>
            </FormControl>
          </Box>
          <List>
            {filteredLogs.length === 0 ? (
              <ListItem>
                <ListItemText primary={activityLogs.length === 0 ? "No activity yet" : "No activity matches filter"} />
              </ListItem>
            ) : (
              filteredLogs.map((log, index) => (
                <React.Fragment key={log.$id}>
                  <ListItem>
                    <ListItemText
                      primary={log.action}
                      secondary={
                        <Box>
                          <Typography variant="caption" color="text.secondary">
                            {log.userName}  {dayjs(log.timestamp).format('MMM D, YYYY h:mm A')}
                          </Typography>
                          {log.oldValue && log.newValue && (
                            <Typography variant="caption" color="text.secondary" sx={{ display: 'block' }}>
                              {log.oldValue}  {log.newValue}
                            </Typography>
                          )}
                        </Box>
                      }
                    />
                    <ListItemSecondaryAction>
                      <Chip label={log.category} size="small" />
                    </ListItemSecondaryAction>
                  </ListItem>
                  {index < filteredLogs.length - 1 && <Divider />}
                </React.Fragment>
              ))
            )}
          </List>
        </Box>
      )}

      {/* Add Task Dialog */}
      <Dialog open={taskDialogOpen} onClose={() => setTaskDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>New Task</DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2, display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              fullWidth
              label="Task Title"
              value={taskFormData.title}
              onChange={(e) => setTaskFormData(prev => ({ ...prev, title: e.target.value }))}
              required
            />
            <TextField
              fullWidth
              label="Description"
              value={taskFormData.description}
              onChange={(e) => setTaskFormData(prev => ({ ...prev, description: e.target.value }))}
              multiline
              rows={3}
            />
            <TextField
              fullWidth
              label="Due Date"
              type="date"
              value={taskFormData.dueDate}
              onChange={(e) => setTaskFormData(prev => ({ ...prev, dueDate: e.target.value }))}
              InputLabelProps={{ shrink: true }}
            />
            <FormControl fullWidth>
              <InputLabel>Priority</InputLabel>
              <Select
                value={taskFormData.priority}
                onChange={(e) => setTaskFormData(prev => ({ ...prev, priority: e.target.value }))}
                label="Priority"
              >
                <MenuItem value="Low">Low</MenuItem>
                <MenuItem value="Medium">Medium</MenuItem>
                <MenuItem value="High">High</MenuItem>
                <MenuItem value="Critical">Critical</MenuItem>
              </Select>
            </FormControl>
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setTaskDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleCreateTask} variant="contained" disabled={loading || !taskFormData.title.trim()}>
            Create
          </Button>
        </DialogActions>
      </Dialog>

      {/* Add Member Dialog */}
      <Dialog open={addMemberDialogOpen} onClose={() => setAddMemberDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Add Member to Project</DialogTitle>
        <DialogContent>
          <TextField
            fullWidth
            label="Email Address"
            type="email"
            value={newMemberEmail}
            onChange={(e) => setNewMemberEmail(e.target.value)}
            sx={{ mt: 2 }}
            placeholder="user@example.com"
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setAddMemberDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleAddMember} variant="contained" disabled={!newMemberEmail.trim()}>
            Add Member
          </Button>
        </DialogActions>
      </Dialog>

      {/* Post Message Dialog */}
      <Dialog open={messageDialogOpen} onClose={() => setMessageDialogOpen(false)} maxWidth="sm" fullWidth>
        <DialogTitle>Post Message</DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2, display: 'flex', flexDirection: 'column', gap: 2 }}>
            <TextField
              fullWidth
              label="Message Date"
              type="date"
              value={messageFormData.messageDate}
              onChange={(e) => setMessageFormData(prev => ({ ...prev, messageDate: e.target.value }))}
              InputLabelProps={{ shrink: true }}
            />
            <TextField
              fullWidth
              label="Message"
              value={messageFormData.message}
              onChange={(e) => setMessageFormData(prev => ({ ...prev, message: e.target.value }))}
              multiline
              rows={4}
              placeholder="Share updates, progress, or important information with your team..."
              required
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setMessageDialogOpen(false)}>Cancel</Button>
          <Button onClick={handleCreateMessage} variant="contained" disabled={loading || !messageFormData.message.trim()}>
            Post
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default ProjectView;

