import { databases } from '../appwrite';
import { Query, ID, Permission, Role } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const PROJECTS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECTS || '691fba2b0003b7a35fc7';

// Generate unique project ID
function generateProjectId() {
  return 'PRJ-' + Date.now().toString(36).toUpperCase() + '-' + Math.random().toString(36).substr(2, 5).toUpperCase();
}

export async function createProject(userId, projectData) {
  try {
    const { name, description, status = 'Not Started', colorTag = '#1976d2', accessGroup = [] } = projectData;
    
    if (!name || !name.trim()) {
      throw new Error('Project name is required');
    }

    const projectId = generateProjectId();
    const now = new Date().toISOString();

    const permissions = [
      Permission.read(Role.user(userId)),
      Permission.update(Role.user(userId)),
      Permission.delete(Role.user(userId)),
      ...accessGroup.map(userId => Permission.read(Role.user(userId))),
      ...accessGroup.map(userId => Permission.update(Role.user(userId)))
    ];

    const document = await databases.createDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      ID.unique(),
      {
        ownerId: userId,
        name: name.trim(),
        description: description || '',
        projectId,
        status,
        colorTag,
        accessGroup: Array.isArray(accessGroup) ? accessGroup : [],
        createdAt: now,
        updatedAt: now
      },
      permissions
    );

    return document;
  } catch (err) {
    console.error('Error creating project:', err);
    throw err;
  }
}

export async function listProjects(userId) {
  try {
    // Get projects where user is owner or in access group
    const ownedProjects = await databases.listDocuments(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      [Query.equal('ownerId', userId)]
    );

    // Get projects where user is in access group
    const accessProjects = await databases.listDocuments(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      [Query.search('accessGroup', userId)]
    );

    // Combine and deduplicate
    const allProjects = [...(ownedProjects.documents || []), ...(accessProjects.documents || [])];
    const uniqueProjects = Array.from(
      new Map(allProjects.map(p => [p.$id, p])).values()
    );

    return uniqueProjects;
  } catch (err) {
    console.error('Error listing projects:', err);
    return [];
  }
}

export async function getProject(projectId) {
  try {
    const project = await databases.getDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      projectId
    );
    return project;
  } catch (err) {
    console.error('Error getting project:', err);
    throw err;
  }
}

export async function updateProject(projectId, projectData) {
  try {
    const updateData = {
      ...projectData,
      updatedAt: new Date().toISOString()
    };

    return await databases.updateDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      projectId,
      updateData
    );
  } catch (err) {
    console.error('Error updating project:', err);
    throw err;
  }
}

export async function deleteProject(projectId) {
  try {
    return await databases.deleteDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      projectId
    );
  } catch (err) {
    console.error('Error deleting project:', err);
    throw err;
  }
}

export async function addUserToAccessGroup(projectId, userEmail) {
  try {
    const project = await databases.getDocument(DATABASE_ID, PROJECTS_COLLECTION_ID, projectId);
    
    // Find user by email
    const PROFILES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROFILES || '6918f632002d42536ab2';
    const profiles = await databases.listDocuments(
      DATABASE_ID,
      PROFILES_COLLECTION_ID,
      [Query.equal('email', userEmail.toLowerCase())]
    );
    
    if (!profiles.documents || profiles.documents.length === 0) {
      throw new Error('User not found with that email');
    }
    
    const userProfile = profiles.documents[0];
    const userId = userProfile.userId;
    
    const currentAccessGroup = project.accessGroup || [];
    if (currentAccessGroup.includes(userId)) {
      throw new Error('User is already in the access group');
    }
    
    const updatedAccessGroup = [...currentAccessGroup, userId];
    
    return await databases.updateDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      projectId,
      {
        accessGroup: updatedAccessGroup,
        updatedAt: new Date().toISOString()
      }
    );
  } catch (err) {
    console.error('Error adding user to access group:', err);
    throw err;
  }
}

export async function removeUserFromAccessGroup(projectId, userId) {
  try {
    const project = await databases.getDocument(DATABASE_ID, PROJECTS_COLLECTION_ID, projectId);
    const currentAccessGroup = project.accessGroup || [];
    const updatedAccessGroup = currentAccessGroup.filter(id => id !== userId);
    
    return await databases.updateDocument(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      projectId,
      {
        accessGroup: updatedAccessGroup,
        updatedAt: new Date().toISOString()
      }
    );
  } catch (err) {
    console.error('Error removing user from access group:', err);
    throw err;
  }
}

export default {
  createProject,
  listProjects,
  getProject,
  updateProject,
  deleteProject,
  addUserToAccessGroup,
  removeUserFromAccessGroup
};

