import { databases } from '../appwrite';
import { Query, ID, Permission, Role } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const MESSAGES_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECT_MESSAGES || '691fbecd000ac88f46a2';

export async function createMessage(messageData) {
  try {
    const {
      projectId,
      taskId = null,
      userId,
      userName,
      message,
      messageDate = null
    } = messageData;

    if (!projectId || !userId || !message || !message.trim()) {
      throw new Error('projectId, userId, and message are required');
    }

    // Get project to set permissions
    const PROJECTS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECTS || '691fba2b0003b7a35fc7';
    const project = await databases.getDocument(DATABASE_ID, PROJECTS_COLLECTION_ID, projectId);

    const permissions = [
      Permission.read(Role.user(project.ownerId)),
      Permission.write(Role.user(project.ownerId)), // Use 'write' instead of 'create'
      Permission.update(Role.user(project.ownerId)),
      Permission.delete(Role.user(project.ownerId)),
      ...(project.accessGroup || []).map(id => Permission.read(Role.user(id))),
      ...(project.accessGroup || []).map(id => Permission.write(Role.user(id))) // Use 'write' instead of 'create'
    ];

    const document = await databases.createDocument(
      DATABASE_ID,
      MESSAGES_COLLECTION_ID,
      ID.unique(),
      {
        projectId,
        taskId: taskId || null,
        userId,
        userName: userName || 'Unknown User',
        message: message.trim(),
        messageDate: messageDate ? new Date(messageDate).toISOString() : new Date().toISOString(),
        createdAt: new Date().toISOString()
      },
      permissions
    );

    return document;
  } catch (err) {
    console.error('Error creating message:', err);
    throw err;
  }
}

export async function listMessages(filters = {}) {
  try {
    const { projectId = null, taskId = null, sortBy = 'newest' } = filters;
    
    const queries = [];
    
    if (projectId) {
      queries.push(Query.equal('projectId', projectId));
    }
    
    if (taskId) {
      queries.push(Query.equal('taskId', taskId));
    }

    const response = await databases.listDocuments(
      DATABASE_ID,
      MESSAGES_COLLECTION_ID,
      queries
    );

    let messages = response.documents || [];

    // Sort
    if (sortBy === 'newest') {
      messages.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
    } else {
      messages.sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt));
    }

    return messages;
  } catch (err) {
    // If collection doesn't exist, return empty array instead of throwing
    if (err.code === 404 || err.message?.includes('not be found')) {
      console.warn('[ProjectMessages] Collection not found, returning empty array');
      return [];
    }
    console.error('Error listing messages:', err);
    return [];
  }
}

export async function updateMessage(messageId, messageData) {
  try {
    return await databases.updateDocument(
      DATABASE_ID,
      MESSAGES_COLLECTION_ID,
      messageId,
      messageData
    );
  } catch (err) {
    console.error('Error updating message:', err);
    throw err;
  }
}

export async function deleteMessage(messageId) {
  try {
    return await databases.deleteDocument(DATABASE_ID, MESSAGES_COLLECTION_ID, messageId);
  } catch (err) {
    console.error('Error deleting message:', err);
    throw err;
  }
}

export default {
  createMessage,
  listMessages,
  updateMessage,
  deleteMessage
};

