import { databases } from '../appwrite';
import { Query, ID, Permission, Role } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const ACTIVITY_LOGS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_ACTIVITY_LOGS || '691fba55002692c63497';

export async function createActivityLog(logData) {
  try {
    const {
      projectId = null,
      taskId = null,
      userId,
      userName,
      category,
      action,
      oldValue = null,
      newValue = null
    } = logData;

    if (!userId || !category || !action) {
      throw new Error('userId, category, and action are required');
    }

    // Get project to set permissions
    let permissions = [Permission.read(Role.users())];
    if (projectId) {
      const PROJECTS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECTS || '691fba2b0003b7a35fc7';
      try {
        const project = await databases.getDocument(DATABASE_ID, PROJECTS_COLLECTION_ID, projectId);
        permissions = [
          Permission.read(Role.user(project.ownerId)),
          ...(project.accessGroup || []).map(id => Permission.read(Role.user(id)))
        ];
      } catch (e) {
        // Project might not exist, use default permissions
      }
    }

    const document = await databases.createDocument(
      DATABASE_ID,
      ACTIVITY_LOGS_COLLECTION_ID,
      ID.unique(),
      {
        projectId: projectId || null,
        taskId: taskId || null,
        userId,
        userName: userName || 'Unknown User',
        category,
        action,
        oldValue: oldValue || null,
        newValue: newValue || null,
        timestamp: new Date().toISOString()
      },
      permissions
    );

    return document;
  } catch (err) {
    console.error('Error creating activity log:', err);
    throw err;
  }
}

export async function listActivityLogs(filters = {}) {
  try {
    const { projectId = null, taskId = null, category = null, userId = null, search = '', sortBy = 'newest' } = filters;
    
    const queries = [];
    
    if (projectId) {
      queries.push(Query.equal('projectId', projectId));
    }
    
    if (taskId) {
      queries.push(Query.equal('taskId', taskId));
    }
    
    if (category) {
      queries.push(Query.equal('category', category));
    }
    
    if (userId) {
      queries.push(Query.equal('userId', userId));
    }

    const response = await databases.listDocuments(
      DATABASE_ID,
      ACTIVITY_LOGS_COLLECTION_ID,
      queries
    );

    let logs = response.documents || [];

    // Search filter (client-side for now)
    if (search) {
      const searchLower = search.toLowerCase();
      logs = logs.filter(log => 
        log.action?.toLowerCase().includes(searchLower) ||
        log.userName?.toLowerCase().includes(searchLower) ||
        log.category?.toLowerCase().includes(searchLower)
      );
    }

    // Sort
    if (sortBy === 'newest') {
      logs.sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
    } else {
      logs.sort((a, b) => new Date(a.timestamp) - new Date(b.timestamp));
    }

    return logs;
  } catch (err) {
    console.error('Error listing activity logs:', err);
    return [];
  }
}

export default {
  createActivityLog,
  listActivityLogs
};

