import { databases } from '../appwrite';
import { Query, ID, Permission, Role } from 'appwrite';

const DATABASE_ID = process.env.REACT_APP_APPWRITE_DATABASE_ID || '69181c670024df0dd32b';
const TASKS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_TASKS || '691fba3e0012d16c1016';

export async function createTask(projectId, taskData, userId) {
  try {
    const {
      title,
      description = '',
      dueDate = null,
      priority = 'Medium',
      status = 'Not Started',
      subtasks = [],
      attachments = []
    } = taskData;

    if (!title || !title.trim()) {
      throw new Error('Task title is required');
    }

    const now = new Date().toISOString();

    // Get project to check permissions
    const PROJECTS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECTS || '691fba2b0003b7a35fc7';
    const project = await databases.getDocument(DATABASE_ID, PROJECTS_COLLECTION_ID, projectId);

    const permissions = [
      Permission.read(Role.user(project.ownerId)),
      Permission.update(Role.user(project.ownerId)),
      Permission.delete(Role.user(project.ownerId)),
      ...(project.accessGroup || []).map(id => Permission.read(Role.user(id))),
      ...(project.accessGroup || []).map(id => Permission.update(Role.user(id)))
    ];

    const documentData = {
      projectId,
      title: title.trim(),
      description,
      dueDate: dueDate ? new Date(dueDate).toISOString() : null,
      priority,
      status,
      subtasks: JSON.stringify(Array.isArray(subtasks) ? subtasks : []),
      attachments: JSON.stringify(Array.isArray(attachments) ? attachments : []),
      createdAt: now,
      updatedAt: now,
      createdBy: userId
    };

    // Only include comments if the attribute exists (it might not due to collection limits)
    // Comments will be handled separately via messages collection
    
    const document = await databases.createDocument(
      DATABASE_ID,
      TASKS_COLLECTION_ID,
      ID.unique(),
      documentData,
      permissions
    );

    return document;
  } catch (err) {
    console.error('Error creating task:', err);
    throw err;
  }
}

export async function listTasks(projectId) {
  try {
    const response = await databases.listDocuments(
      DATABASE_ID,
      TASKS_COLLECTION_ID,
      [Query.equal('projectId', projectId)]
    );

    const tasks = (response.documents || []).map(task => {
      const taskObj = {
        ...task,
        subtasks: task.subtasks ? JSON.parse(task.subtasks) : [],
        attachments: task.attachments ? JSON.parse(task.attachments) : []
      };
      // Don't try to parse comments - it doesn't exist in the schema
      // Comments are handled via projectMessages collection
      return taskObj;
    });

    return tasks;
  } catch (err) {
    console.error('Error listing tasks:', err);
    return [];
  }
}

export async function getTask(taskId) {
  try {
    const task = await databases.getDocument(DATABASE_ID, TASKS_COLLECTION_ID, taskId);
    return {
      ...task,
      subtasks: task.subtasks ? JSON.parse(task.subtasks) : [],
      attachments: task.attachments ? JSON.parse(task.attachments) : []
      // Comments are handled via projectMessages collection, not stored in task
    };
  } catch (err) {
    console.error('Error getting task:', err);
    throw err;
  }
}

export async function updateTask(taskId, taskData) {
  try {
    const updateData = {
      ...taskData,
      updatedAt: new Date().toISOString()
    };

    // Stringify arrays
    if (updateData.subtasks) {
      updateData.subtasks = JSON.stringify(Array.isArray(updateData.subtasks) ? updateData.subtasks : []);
    }
    if (updateData.attachments) {
      updateData.attachments = JSON.stringify(Array.isArray(updateData.attachments) ? updateData.attachments : []);
    }
    // Comments are handled via messages collection, don't include in task update
    delete updateData.comments;

    if (updateData.dueDate) {
      updateData.dueDate = new Date(updateData.dueDate).toISOString();
    }

    return await databases.updateDocument(
      DATABASE_ID,
      TASKS_COLLECTION_ID,
      taskId,
      updateData
    );
  } catch (err) {
    console.error('Error updating task:', err);
    throw err;
  }
}

export async function deleteTask(taskId) {
  try {
    return await databases.deleteDocument(DATABASE_ID, TASKS_COLLECTION_ID, taskId);
  } catch (err) {
    console.error('Error deleting task:', err);
    throw err;
  }
}

export async function getOverdueTasks(userId) {
  try {
    // Get all projects user has access to
    const PROJECTS_COLLECTION_ID = process.env.REACT_APP_APPWRITE_COLLECTION_PROJECTS || '691fba2b0003b7a35fc7';
    const ownedProjects = await databases.listDocuments(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      [Query.equal('ownerId', userId)]
    );
    const accessProjects = await databases.listDocuments(
      DATABASE_ID,
      PROJECTS_COLLECTION_ID,
      [Query.search('accessGroup', userId)]
    );
    const allProjects = [...(ownedProjects.documents || []), ...(accessProjects.documents || [])];
    const projectIds = Array.from(new Set(allProjects.map(p => p.$id)));

    // Get all tasks from these projects
    const now = new Date().toISOString();
    const overdueTasks = [];
    
    for (const projectId of projectIds) {
      const tasks = await listTasks(projectId);
      const overdue = tasks.filter(task => 
        task.dueDate && 
        new Date(task.dueDate) < new Date(now) && 
        task.status !== 'Completed'
      );
      overdueTasks.push(...overdue);
    }

    return overdueTasks;
  } catch (err) {
    console.error('Error getting overdue tasks:', err);
    return [];
  }
}

export default {
  createTask,
  listTasks,
  getTask,
  updateTask,
  deleteTask,
  getOverdueTasks
};

